import cx from "classnames";
import { useTranslation } from "react-i18next"
import { useCookies } from "react-cookie";
import { Button } from "react-materialize"
import React, { useEffect } from 'react'
import styles from "../../css.module/language.module.css"

const LanguageSelector = () => {

    const [cookies, setCookie] = useCookies(['language'])
    const { t, i18n } = useTranslation()

    useEffect(() => {
        if(!cookies.language)
        changeLanguage(cookies.language)
    }, [])

    const changeLanguage = (lang) => {
        setCookie('language', lang)
        i18n.changeLanguage(lang)
    }

    return <>{cookies.language === "ua" ?
        <Button tooltip={t("Змінити мову")} className={cx(styles.ua, "transparent")} onClick={() => changeLanguage("ru")}
            tooltipOptions={{ position: 'left' }} /> :
        <Button tooltip={t("Изменить язык")} className={cx(styles.ru, "transparent")} onClick={() => changeLanguage("ua")}
            tooltipOptions={{ position: 'left' }} />}</>
}

export default LanguageSelector
